import ylRequest from '../index'
import { IAccount } from './type'
import LocalCache from '../../utils/cache'
import userMenus from '@/assets/data/userMenus'
import userInfo from '@/assets/data/userInfo'

enum LoginAPI {
  AccountLogin = '/login'
}

export function accountLoginRequest(account: IAccount) {
  // return ylRequest.post({
  //   url: LoginAPI.AccountLogin,
  //   data: account
  // })
  const names = [...Object.keys(userMenus)]
  console.log(names)
  const token = account.name + "'s token"
  const name: 'kirito' | 'ambition' = LocalCache.getCache('name')
  const userMenusPersonal = userMenus[name]
  const userInfoPersonal = userInfo[name]
  LocalCache.setCache('token', token)
  LocalCache.setCache('userMenus', userMenusPersonal)
  LocalCache.setCache('userInfo', userInfoPersonal)

  return {
    data: {
      token,
      name: account.name,
      userMenusPersonal,
      userInfoPersonal
    }
  }
}
