import axios from 'axios'
import type { AxiosInstance, AxiosRequestConfig } from 'axios'
import { YLRequestInterceptors, YLRequestConfig } from './type'
import { ElLoading } from 'element-plus'

class YLRequest {
  instance: AxiosInstance
  interceptors?: YLRequestInterceptors
  loading: any
  isShowLoading: boolean
  constructor(config: YLRequestConfig) {
    this.instance = axios.create(config)
    this.interceptors = config.interceptors
    this.instance.interceptors.request.use(
      this.interceptors?.requestInterceptor,
      this.interceptors?.requestInterceptorCatch
    )
    this.instance.interceptors.response.use(
      this.interceptors?.responseInterceptor,
      this.interceptors?.responseInterceptorCatch
    )
    this.isShowLoading = config.isShowLoading ?? false
    //添加所有实例都有的拦截器
    this.instance.interceptors.request.use(
      (config: YLRequestConfig) => {
        // console.log('所以实例都有的拦截器:请求成功!')
        this.isShowLoading = config.isShowLoading ?? false
        if (this.isShowLoading) {
          this.loading = ElLoading.service({
            lock: true,
            text: 'Loading',
            background: 'rgba(0, 0, 0, 0.7)'
          })
        }

        return config
      },
      (err) => {
        // console.log('所有实例都有的拦截器:请求失败!')
        return err
      }
    )
    this.instance.interceptors.response.use(
      (res) => {
        const data = res.data
        // console.log('所有实例都有的拦截器:响应成功!')
        // setTimeout(() => {
        //   this.loading?.close()
        // }, 500)
        this.loading?.close()
        if (data.returnCode === '-1001') {
          console.log('请求失败~,')
        } else {
          return data.returnCode
        }
        return data
      },
      (err) => {
        // console.log('所有实例都有的拦截器:响应失败')
        setTimeout(() => {
          this.loading?.close()
        }, 500)
        if (err.response.status === 404) {
          console.log('错误代码404')
        } else {
          return err.response.status
        }
        return err
      }
    )
  }

  request<T>(config: YLRequestConfig<T>): Promise<T> {
    return new Promise((resolve, reject) => {
      if (config.interceptors?.requestInterceptor) {
        config = config.interceptors.requestInterceptor(config)
      }
      this.instance
        .request<any, T>(config)
        .then((res) => {
          if (config.interceptors?.responseInterceptor) {
            res = config.interceptors.responseInterceptor(res)
          }
          // console.log(res)
          resolve(res)
        })
        .catch((err) => {
          reject(err)
          return err
        })
    })
  }

  get<T>(config: YLRequestConfig<T>): Promise<T> {
    return this.request<T>({ ...config, method: 'GET' })
  }
  post<T>(config: YLRequestConfig<T>): Promise<T> {
    return this.request<T>({ ...config, method: 'POST' })
  }
  delete<T>(config: YLRequestConfig<T>): Promise<T> {
    return this.request<T>({ ...config, method: 'DELETE' })
  }
  patch<T>(config: YLRequestConfig<T>): Promise<T> {
    return this.request<T>({ ...config, method: 'PATCH' })
  }
}

export default YLRequest
